import uniNavBar from '@/components/uni-nav-bar/uni-nav-bar.vue';
import uniGrid from '@/components/uni-grid/uni-grid.vue';
import uniGridItem from '@/components/uni-grid-item/uni-grid-item.vue';
import mixPulldownRefresh from '@/components/mix-pulldown-refresh/mix-pulldown-refresh';
import mixLoadMore from '@/components/mix-load-more/mix-load-more';
import json from '@/json';
export default {
	components: {
		uniNavBar,
		uniGrid,
		uniGridItem,
		mixPulldownRefresh,
		mixLoadMore,
	},
	data() {
		return {
			imageList: [],
			value:['请选择', '请选择', '请选择'],
			name: '',
			idCard: '',
			phone: '',
			
			sexIndex: 0,
            options:[],
			workIndex: 0,
			dynamicList: [],
			list: [],
			defaultProps1:{"label":"name","value":"id","children":"child"},
			srcId:'',
			trues: true,
			tabCurrentIndex: 0, //当前选项卡索引
			scrollLeft: 0, //顶部选项卡左滑距离
			enableScroll: true,
			tabBars: [],
			pageNo:1
		};
	},
	async onLoad() {
		// 获取屏幕宽度
		windowWidth = uni.getSystemInfoSync().windowWidth;
		
	},
	methods: {
		/**
		 * 数据处理方法在vue和nvue中通用，可以直接用mixin混合
		 * 这里直接写的
		 * mixin使用方法看index.nuve
		 */
		//获取分类
		loadTabbars() {
			let tabList = json.tabList;
			tabList.forEach(item => {
				item.newsList = [];
				item.loadMoreStatus = 0; //加载更多 0加载前，1加载中，2没有更多了
				item.refreshing = 0;
			});
			this.tabBars = tabList;
			this.loadNewsList('add');
		},
		loadNewsList(type) {
			console.log("实时监控数据")
			let tabItem = this.tabBars[this.tabCurrentIndex];
			
			//type add 加载更多 refresh下拉刷新
			if (type === 'add') {
				if (tabItem.loadMoreStatus === 2) {
					return;
				}
				tabItem.loadMoreStatus = 1;
			}






			var obj={id: this.srcId,pageNo:this.pageNo}
			if(this.srcId.length==0){
				delete obj.id 
			}
			
			this.$http
				.get('/interface/rest/http/xlwb/xlgc-wb-xcx-yjqz-ssjksp-x.htm', {params:obj})
				.then(res => {
					console.log(res);
					if (res.data.msgState == 1) {
						var list = res.data.list;
						console.log('list');
						console.log(list);
						
						
						if (type === 'refresh') {
							tabItem.newsList = []; //刷新前清空数组
						}
						list.forEach(item => {
							
							tabItem.newsList.push(item);
						});
						this.pageNo+=1
					//下拉刷新 关闭刷新动画
					if (type === 'refresh') {
						this.$refs.mixPulldownRefresh && this.$refs.mixPulldownRefresh.endPulldownRefresh();



						if(res.data.totalPages===0||res.data.curPage===res.data.totalPages){
						tabItem.loadMoreStatus =2	
						}else tabItem.loadMoreStatus = 0;
						
						
					}
						//上滑加载 处理状态
						if (type === 'add') {
							if(res.data.totalPages===0||res.data.curPage===res.data.totalPages){
							tabItem.loadMoreStatus =2	
							this.pageNo=1
							}
							}
					} else {
						uni.showLoading({
							title: '请求失败'
						});
					}
				})
				.catch(err => {
					console.log(err);
					console.log(err);
					uni.showToast({
						icon:"none",
						title: '监控列表获取失败！',
						duration:2000
					});
				});
		},
		
		ontrueGetList() {
			console.log('实时监控');
			this.init()
			this.getType();
			this.loadTabbars();
			
			
			//console.log(json.subs);
			//this.list = json.subs;
			//this.getList()
		},
		init(){
			this.value=['请选择', '请选择', '请选择']
			this.srcId=''
			this.pageNo=1
		},
		getType() {
			this.$http
				.get('/interface/rest/http/xlwb/xlgc-wb-xcx-sssj-xzcjl.htm', { params: {} })
				.then(res => {
					//console.log('级联数据');
					//console.log(res);
					if (res.data.msgState == 1) {
						 this.options=res.data.result
						 console.log(this.options)
						// var obj1 = [{ id: '0', type: '点击选择' }];
						// var data = res.data.list;
						// var obj = [...obj1, ...data];
						// this.type1 = this.typeChange(obj);
						// this.type2 = this.typeChange(obj);
					} else {
						uni.showToast({
							icon:"none",
							title: '联动数据获取失败！',
							duration:2000
						});
						
					}
				})
				.catch(err => {
					console.log(err);
				});
		},
		getList(){
			var obj={id: this.srcId,pageNo:this.pageNo}
			if(this.srcId.length==0){
				delete obj.id 
			}
		this.$http
			.get('/interface/rest/http/xlwb/xlgc-wb-xcx-yjqz-ssjksp-x.htm', { params: obj })
			.then(res => {
				//console.log('监控列表数据');
				//console.log(res);
				if (res.data.msgState == 1) {
					 this.list=res.data.list
					console.log(this.list)
					
				} else {
					uni.showToast({
						icon:"none",
						title: '监控列表获取失败！',
						duration:2000
					});
					
				}
			})
			.catch(err => {
				console.log(err);
				uni.showToast({
					icon:"none",
					title: '监控列表获取失败！',
					duration:2000
				});
			});	
		},
		lower() {
			uni.showToast({
				title: 'scroll-view的加载更多'
			});
			console.log('分页数据可以放这里~~~~~~嘿嘿');
		},
		sizeTypeChange(e) {
			this.sexIndex = parseInt(e.detail.value);
		},
		change(e) {
			let { index } = e.detail;
			this.list[index].badge && this.list[index].badge++;

			uni.showToast({
				title: `点击第${index + 1}个宫格`,
				icon: 'none'
			});
		},
		videoDetail(item ) {
			let data = {
				item:item
			};
			let url = 'video-details';

			uni.navigateTo({
				url: `/pages/video-details/${url}?data=${JSON.stringify(data)}`
			});
		},
		showPicker() {
			this.$refs.linkage.show();
		},
		onConfirm($event,val){
			console.log($event);
				console.log(val)
			var arr=[];
			arr.push($event.obj.col1.name)
			arr.push($event.obj.col2.name)
			arr.push($event.obj.col3.name)
			this.value=arr;
			console.log(this.value);
		this.srcId=$event.value[2]
		
			//TODO在这里发起请求
			this.onPulldownReresh()
		},
		onCancel(){
			console.log('你取消了')
		},
		//下拉刷新
		onPulldownReresh() {
			this.pageNo=1;
			this.loadNewsList('refresh');
		},
		//上滑加载
		loadMore() {
			this.loadNewsList('add');
		},
		//设置scroll-view是否允许滚动，在小程序里下拉刷新时避免列表可以滑动
		setEnableScroll(enable) {
			if (this.enableScroll !== enable) {
				this.enableScroll = enable;
			}
		},
		
		
	}
};
